@import "https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200..1000;1,200..1000&display=swap";
.forecast {
  height: 150vh;
  padding: 63px 80px;
}

.forecast__wrapper {
  justify-content: space-between;
  align-items: center;
  gap: 50px;
  display: flex;
}

.forecast__btn {
  width: 99px;
  height: 38px;
  cursor: pointer;
  background: #d9d9d9;
  border: 1px solid #000;
  border-radius: 40px;
  padding-top: 2.6px;
}

.forecast__circle {
  width: 30px;
  height: 30px;
  background-color: #111;
  border-radius: 100%;
  transition: all .5s;
  display: inline-block;
  transform: translateX(-23px);
}

.forecast__dark {
  transition: all .5s;
  transform: translateX(23px);
}

.forecast__form {
  position: relative;
}

.forecast__form button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  top: 10px;
  left: 18px;
}

.forecast__form input {
  color: #292929;
  width: 803px;
  height: 62px;
  background: #d9d9d9;
  border: 1px solid #000;
  border-radius: 40px;
  padding-left: 80px;
  font-size: 18px;
  box-shadow: 0 4px 40px rgba(0, 0, 0, .25);
}

.forecast__loc {
  color: #fff;
  cursor: pointer;
  background: #4cbb17;
  border: none;
  border-radius: 40px;
  align-items: center;
  gap: 4px;
  padding: 14px 25px;
  display: flex;
  box-shadow: 0 4px 40px rgba(0, 0, 0, .25);
}

.forecast__list {
  max-width: 100%;
  background-color: #d9d9d9;
  border-radius: 30px;
  justify-content: center;
  gap: 20px;
  margin-top: 50px;
  padding: 35px 55px;
  display: flex;
}

.forecast__list li {
  text-align: center;
  max-width: 150px;
  background: linear-gradient(145deg, #f88508 0%, rgba(246, 250, 217, 0) 100%);
  border-radius: 60px;
  padding: 25px 10px;
}

.dark-mode {
  background: linear-gradient(110.05deg, #383838 0%, rgba(158, 158, 158, 0) 71.82%);
}

.light-mode {
  background: linear-gradient(135deg, #fff 0%, rgba(70, 97, 115, 0) 100%);
  transition: all .5s;
}

#js-light {
  text-align: center;
}

* {
  box-sizing: border-box;
  font-family: Nunito;
}

body, h2, h3 {
  margin: 0;
}

p {
  margin: 6px;
}

ul {
  margin: 0;
